import React, { useEffect } from "react";
import styled from "@emotion/styled";
import { headerIndexState } from "../../recoil";
import { useSetRecoilState } from "recoil";
import useRightEndRendered from "../../hooks/useRightEndRendered";

const AboutImage = ({ aboutDefinitionRef }) => {
  // ** recoil
  const setHeaderIndex = useSetRecoilState(headerIndexState);

  // ** ref
  const ref = React.useRef<HTMLDivElement>(null);
  const isAboutImageRightEndRendered = useRightEndRendered(ref);
  const isDefinitionRightEndRendered = useRightEndRendered(aboutDefinitionRef);

  // ** effect
  useEffect(() => {
    if (isDefinitionRightEndRendered) setHeaderIndex(1);
    else setHeaderIndex(0);
  }, [isDefinitionRightEndRendered]);

  return (
    <_Frame ref={ref}>
      <_FixedImage
        className={
          (isDefinitionRightEndRendered && "absolute") ||
          (isAboutImageRightEndRendered && "fixed") ||
          ""
        }
      />
    </_Frame>
  );
};
export default AboutImage;

const _Frame = styled.div`
  width: calc(92vh * 1.4);
  height: 92vh;
`;

const _FixedImage = styled.div<any>`
  &.fixed {
    position: fixed;
  }

  &.absolute {
    position: absolute;
  }
  position: relative;
  right: 0;
  background-image: url("/sample/metasky-landing/images/bg-metasky-logo.png");
  background-size: cover;
  background-repeat: no-repeat;
  height: 92vh;
  padding-left: calc(92vh * 1.4);
  opacity: 0.5;
`;
