import Header from "./components/Header";
import AboutContianer from "./containers/AboutContainer";
import styled from "@emotion/styled";
import useResize from "./hooks/useResize";
import ProjectContianer from "./containers/ProjectContainer";
import useScrollYtoX from "./hooks/useScrollYtoX";
import React, { useEffect, useState } from "react";
import RecruitContainer from "./containers/RecruitContainer";
import ContactContianer from "./containers/ContactContainer";

export interface headerRefsProps {
  about: React.MutableRefObject<HTMLDivElement>;
  project: React.MutableRefObject<HTMLDivElement>;
  recruit: React.MutableRefObject<HTMLDivElement>;
  contact: React.MutableRefObject<HTMLDivElement>;
}

const DesktopApp = () => {
  useResize();
  useScrollYtoX();
  const headerRefs: headerRefsProps = {
    about: React.useRef<HTMLDivElement>(null),
    project: React.useRef<HTMLDivElement>(null),
    recruit: React.useRef<HTMLDivElement>(null),
    contact: React.useRef<HTMLDivElement>(null),
  };

  return (
    <_Column>
      <Header headerRefs={headerRefs} />
      <_Row id="row">
        <AboutContianer containerRef={headerRefs.about} />
        <ProjectContianer containerRef={headerRefs.project} />
        <RecruitContainer containerRef={headerRefs.recruit} />
        <ContactContianer containerRef={headerRefs.contact} />
      </_Row>
    </_Column>
  );
};

const _Column = styled.div`
  display: flex;
  flex-direction: column;
  height: 100vh;
  width: fit-content;
  white-space: nowrap;
`;

const _Row = styled.div`
  display: flex;
  flex-direction: row;
`;

export default DesktopApp;
