import styled from "@emotion/styled";
import React from "react";
import Header from "./mobile/components/Header";
import About from "./mobile/components/About";
import Project from "./mobile/components/Project";
import Recruit from "./mobile/components/Recruit";
import Contact from "./mobile/components/Contact";

export interface mobileHeaderRefsProps {
  about: React.MutableRefObject<HTMLDivElement>;
  project: React.MutableRefObject<HTMLDivElement>;
  recruit: React.MutableRefObject<HTMLDivElement>;
  contact: React.MutableRefObject<HTMLDivElement>;
}

const MobileApp = () => {
  const mobileHeaderRefs: mobileHeaderRefsProps = {
    about: React.useRef<HTMLDivElement>(null),
    project: React.useRef<HTMLDivElement>(null),
    recruit: React.useRef<HTMLDivElement>(null),
    contact: React.useRef<HTMLDivElement>(null),
  };

  return (
    <_Column>
      <_HeaderSpacer />
      <About ref={mobileHeaderRefs.about} />
      <Project ref={mobileHeaderRefs.project} />
      <Recruit ref={mobileHeaderRefs.recruit} />
      <Contact ref={mobileHeaderRefs.contact} />

      <Header headerRefs={mobileHeaderRefs} />
    </_Column>
  );
};

export default MobileApp;

const _Column = styled.div`
  display: flex;
  flex-direction: column;
  width: 100%;
  white-space: nowrap;
`;

const _HeaderSpacer = styled.div`
  width: 100%;
  height: 50px;
`;
