import { domStateSelector } from "../recoil";
import { useEffect, useState } from "react";
import { useRecoilCallback, useRecoilValue } from "recoil";

const useScrollYtoX = () => {
  var isTrackpad = false;

  useEffect(() => {
    const handleYtoX = (e) => {
      // block default event, mouse xscroll(Trackpad)
      e.preventDefault();

      // ** parse environment
      if (e.wheelDeltaY) {
        if (e.wheelDeltaY === e.deltaY * -3) {
          isTrackpad = true;
        }
      } else if (e.deltaMode === 0) {
        isTrackpad = true;
      }
      console.log(isTrackpad ? "Trackpad detected" : "Mousewheel detected");

      // ** variable
      let mouseScrollspeed = 600;
      let moveAmount = isTrackpad
        ? e.deltaY
        : Math.sign(e.deltaY) * mouseScrollspeed;

      const recruitContainer =
        window.document.querySelector("#recruit-container");
      const recruitPosition = {
        bottom: recruitContainer.scrollHeight,
        left: recruitContainer.getClientRects()[0].left,
      };

      // ** conditions
      let isRecruitTopEnd = recruitContainer.scrollTop === 0;
      let isRecruitBottomEnd =
        Math.abs(
          recruitContainer.scrollTop +
            recruitContainer.clientHeight -
            recruitPosition.bottom
        ) <= 1;
      let isRecruitLeftEnd =
        recruitPosition.left < 2 && recruitPosition.left > -2;
      let isRecruitLeftEndCloseByLeft =
        Math.abs(recruitPosition.left) < Math.abs(moveAmount) && e.deltaY > 0;
      let isRecruitLeftEndCloseByRight =
        Math.abs(recruitPosition.left) < Math.abs(moveAmount) && e.deltaY < 0;

      // ** function
      const xMove = () => {
        // 빠른실패 - yMove 조건일때 xMove 발생 방지
        if (
          (isRecruitLeftEnd && isRecruitTopEnd && moveAmount > 0) ||
          (isRecruitLeftEnd && isRecruitBottomEnd && moveAmount < 0)
        )
          return;

        // 움직임 한계값 제한 - 왼쪽에서 Recruit 컨테이너에 부딛힐 때
        if (isRecruitLeftEndCloseByLeft && !isRecruitBottomEnd)
          moveAmount = Math.abs(recruitPosition.left);
        // 움직임 한계값 제한 - 오른쪽에서 Recruit 컨테이너에 부딛힐 때
        else if (isRecruitLeftEndCloseByRight && !isRecruitTopEnd)
          moveAmount = -1 * Math.abs(recruitPosition.left);

        window.scrollTo({
          left: moveAmount + window.scrollX,
          behavior: isTrackpad ? "instant" : "smooth",
        });
      };

      const yMove = () => {
        recruitContainer.scrollBy({
          top: moveAmount,
          behavior: isTrackpad ? "instant" : "smooth",
        });
      };

      if (
        // true인 경우 Recruit에 위치
        // isRecruitTopEnd, 내가 보고있는 시점과 Recruit의 top이 같은 경우
        // isRecruitBottomEnd, 내가 보고있는 시점과 Recruit의 bottom이 같은 경우
        isRecruitLeftEnd === false ||
        (isRecruitLeftEnd && isRecruitTopEnd && e.deltaY < 0) ||
        (isRecruitLeftEnd && isRecruitBottomEnd && e.deltaY > 0)
      ) {
        xMove();
      } else {
        yMove();
      }
    };

    // window.addEventListener("wheel", handleYtoX, { passive: false });
    window.addEventListener("mousewheel", handleYtoX, { passive: false });

    return () => {
      window.removeEventListener("mousewheel", handleYtoX);
    };
  }, []); // TODO
};

export default useScrollYtoX;
