import AboutIntro from "../components/about/AboutIntro";
import AboutImage from "../components/about/AboutImage";
import AboutDefinition from "../components/about/AboutDefinition";
import Layout from "../components/Layout";
import React, { useEffect } from "react";
import useRightEndRendered from "../hooks/useRightEndRendered";
import { headerIndexState } from "../recoil";
import { useSetRecoilState } from "recoil";

const AboutContianer = ({ containerRef }) => {
  const aboutDefinitionRef = React.useRef<HTMLDivElement>(null);

  const setHeaderIndex = useSetRecoilState(headerIndexState);
  const isRightEndRendered = useRightEndRendered(containerRef);
  useEffect(() => {
    if (isRightEndRendered) setHeaderIndex(1);
    else setHeaderIndex(0);
  }, [isRightEndRendered]);

  return (
    <Layout ref={containerRef}>
      <AboutIntro />
      <AboutImage aboutDefinitionRef={aboutDefinitionRef} />
      <AboutDefinition ref={aboutDefinitionRef} />
    </Layout>
  );
};

export default AboutContianer;
