import React from "react";

const useBottomEndRendered = (ref: React.RefObject<any>): boolean => {
  const [isBottomEndRendered, setIsBottomEndRendered] = React.useState(false);

  const scrollEvent = React.useCallback(() => {
    const scroll = ref.current?.getBoundingClientRect();

    if (scroll) {
      // Check if the bottom end of the element has reached the bottom end of the viewport
      const isBottomEndReached = scroll.bottom <= window.innerHeight; // if scroll.bottom is less than or equal to window.innerHeight, it means the bottom of the element has reached the bottom of the viewport.
      setIsBottomEndRendered(isBottomEndReached);
    }
  }, [ref]);

  React.useEffect(() => {
    scrollEvent();
    window.addEventListener("scroll", scrollEvent);
    return () => {
      window.removeEventListener("scroll", scrollEvent);
    };
  }, [scrollEvent]);

  return isBottomEndRendered;
};

export default useBottomEndRendered;
