import { headerIndexState } from "../recoil";
import styled from "@emotion/styled";
import react from "react";
import { useRecoilState } from "recoil";
import { headerRefsProps } from "../DesktopApp";

interface HeaderProps {
  headerRefs: headerRefsProps;
}

const Header: React.FC<HeaderProps> = ({ headerRefs }) => {
  // ** recoil
  const [headerIndex, setHeaderIndex] = useRecoilState(headerIndexState);

  return (
    <>
      <_HeaderFrame>
        <_HeaderWrap>
          <button
            onClick={() => {
              document.querySelector("#recruit-container").scrollTo(0, 0);
              headerRefs.about.current?.scrollIntoView({
                behavior: "smooth",
                block: "start",
                inline: "start",
              });
            }}
            className={(headerIndex == 0 && "active") || ""}
          >
            About
          </button>
          <button
            onClick={() => {
              document.querySelector("#recruit-container").scrollTo(0, 0);
              headerRefs.project.current?.scrollIntoView({
                behavior: "smooth",
                block: "start",
                inline: "start",
              });
            }}
            className={(headerIndex == 1 && "active") || ""}
          >
            Project
          </button>
          <button
            onClick={() => {
              document.querySelector("#recruit-container").scrollTo(0, 0);
              headerRefs.recruit.current?.scrollIntoView({
                behavior: "smooth",
                block: "start",
                inline: "start",
              });
            }}
            className={(headerIndex == 2 && "active") || ""}
          >
            Recruit
          </button>
          <button
            onClick={() => {
              document
                .querySelector("#recruit-container")
                .scrollTo(
                  0,
                  document.querySelector("#recruit-container").scrollHeight
                );

              headerRefs.contact.current?.scrollIntoView({
                behavior: "smooth",
                block: "start",
                inline: "start",
              });
            }}
            className={(headerIndex == 3 && "active") || ""}
          >
            Contact
          </button>
        </_HeaderWrap>
      </_HeaderFrame>
    </>
  );
};

export default Header;

const _HeaderFrame = styled.div`
  position: fixed;
  width: 100vw;
  height: 8vh;
  display: flex;
  background-color: #000;
  align-items: center;
  justify-content: center;
  z-index: 100;
  padding-bottom: 1.8vh;
`;

const _HeaderWrap = styled.div`
  font-size: 3.5;
  display: flex;
  gap: 8.6vh;
  & button {
    display: flex;
    flex-direction: column;
    align-items: center;
    line-height: 1.25;
    font-size: 2vh;
    font-family: Montserrat;
    font-weight: 600;
    transform: translateY(0.4vh);
    color: rgba(255, 255, 255, 0.5);
    position: relative;
  }
  & button:hover,
  & button.active {
    color: #ff0;
  }
  & button:hover::after,
  & button.active::after {
    content: "●";
    font-size: 1.06vh;
    position: absolute;
    top: 2.6vh;
  }
`;
