import Layout from "../components/Layout";
import RecruitList from "../components/recruit/RecruitList";
import useLeftEndRendered from "../hooks/useLeftEndRendered";
import useRightEndRendered from "../hooks/useRightEndRendered";
import { browserStatusState, headerIndexState } from "../recoil";
import { useEffect, useRef } from "react";
import { useRecoilState, useRecoilValue, useSetRecoilState } from "recoil";

const RecruitContianer = ({ containerRef }) => {
  const listRef = useRef();
  const listBottomRef = useRef();

  const browserStatus = useRecoilValue(browserStatusState);
  const setHeaderIndex = useSetRecoilState(headerIndexState);
  const isRightEndRendered = useRightEndRendered(containerRef);
  const isLeftEndRendered = useLeftEndRendered(containerRef);

  useEffect(() => {
    if (isRightEndRendered) setHeaderIndex(3);
    else if (isLeftEndRendered) setHeaderIndex(2);
  }, [isRightEndRendered]);

  return (
    <Layout ref={containerRef}>
      <RecruitList ref={listRef} />
      <div ref={listBottomRef}></div>
    </Layout>
  );
};

export default RecruitContianer;
