import Layout from "../components/Layout";
import ProjectList from "../components/project/ProjectList";
import ProjectPartnership from "../components/project/ProjectPartnership";
import ProjectHistory from "../components/project/ProjectHistory";
import { useEffect } from "react";
import useRightEndRendered from "../hooks/useRightEndRendered";
import { headerIndexState } from "../recoil";
import { useSetRecoilState } from "recoil";
import useLeftEndRendered from "../hooks/useLeftEndRendered";

const ProjectContianer = ({ containerRef }) => {
  const setHeaderIndex = useSetRecoilState(headerIndexState);
  const isRightEndRendered = useRightEndRendered(containerRef);
  const isLeftEndRendered = useLeftEndRendered(containerRef);
  useEffect(() => {
    if (isRightEndRendered) setHeaderIndex(2);
    else if (isLeftEndRendered) setHeaderIndex(1);
  }, [isRightEndRendered]);
  return (
    <Layout ref={containerRef}>
      <ProjectList />
      <ProjectPartnership />
      <ProjectHistory />
    </Layout>
  );
};

export default ProjectContianer;
