import React from "react";

const useTopEndRendered = (ref: React.RefObject<any>): boolean => {
  const [isTopEndRendered, setIsTopEndRendered] = React.useState(false);

  const scrollEvent = React.useCallback(() => {
    const scroll = ref.current?.getBoundingClientRect();

    if (scroll) {
      // Check if the Top end of the image element has reached the Top end of the browser window
      const isTopEndReached = scroll.top <= 0; // if scroll.top is less than or equal to 0, it means the top of the element has reached the top of the viewport.
      setIsTopEndRendered(isTopEndReached);
    }
  }, [ref]);

  React.useEffect(() => {
    scrollEvent();
    window.addEventListener("scroll", scrollEvent);
    return () => {
      window.removeEventListener("scroll", scrollEvent);
    };
  }, [scrollEvent]);

  return isTopEndRendered;
};

export default useTopEndRendered;
