import { atom, selector, useRecoilState, useRecoilValue } from "recoil";

// ANCHOR atoms
export const browserStatusState = atom({
  key: "browserStatusState",
  default: {
    // 브라우저 가로 길이
    width: 0,
    // 브라우저 세로 길이
    height: 0,

    // 모바일 유무
    isMobile: false,
  },
});

export const headerIndexState = atom({
  key: "headerIndexState",
  default: 0,
});
export const domStatesState = atom({
  key: "domStatesState",
  default: [],
});

export const domStateSelector = selector({
  key: "domStateSelector",
  get: ({ get }) => get(domStatesState),
  set: ({ get, set }, newVariable: any) => {
    const domStates = get(domStatesState);
    if (typeof newVariable === "string") {
      if (!domStates.includes(newVariable)) {
        set(domStatesState, [...domStates, newVariable]);
      }
    } else if (
      typeof newVariable === "object" &&
      newVariable.hasOwnProperty("remove")
    ) {
      const updated = domStates.filter((state) => state !== newVariable.remove);
      set(domStatesState, updated);
    }
  },
});
