import React from "react";
import styled from "@emotion/styled";
import { browserStatusState } from "../recoil";
import { useRecoilValue } from "recoil";

const useLeftEndRendered = (ref: React.RefObject<any>): boolean => {
  const [isLeftEndRendered, setIsLeftEndRendered] = React.useState(false);
  const browserStatus = useRecoilValue(browserStatusState);

  const scrollEvent = React.useCallback(() => {
    const scroll = ref.current?.getBoundingClientRect();

    if (scroll) {
      // Check if the left end of the image element has reached the left end of the browser window
      const isLeftEndReached = scroll.left <= browserStatus.width;
      setIsLeftEndRendered(isLeftEndReached);
    }
  }, [browserStatus.width, ref]);

  React.useEffect(() => {
    scrollEvent();
    window.addEventListener("scroll", scrollEvent);
    return () => {
      window.removeEventListener("scroll", scrollEvent);
    };
  }, [scrollEvent]);

  return isLeftEndRendered;
};

export default useLeftEndRendered;
