import styled from "@emotion/styled";
import react, { ForwardedRef, forwardRef } from "react";
import Header from "./Header";

const Layout = forwardRef(
  (props: React.PropsWithChildren, ref: ForwardedRef<HTMLDivElement>) => {
    return (
      <>
        <_ContentFrame ref={ref}>{props.children}</_ContentFrame>
      </>
    );
  }
);

export default Layout;

const _ContentFrame = styled.div<any>`
  position: relative;
  display: flex;
  width: fit-content;
  height: 100vh;
  white-space: nowrap;
  padding-top: 8vh;
  overflow-y: hidden;
`;
