import React from "react";
import styled from "@emotion/styled";
import { browserStatusState } from "../recoil";
import { useRecoilValue } from "recoil";

const useRightEndRendered = (ref: React.RefObject<any>): boolean => {
  const [isRightEndRendered, setIsRightEndRendered] = React.useState(false);
  const browserStatus = useRecoilValue(browserStatusState);

  const scrollEvent = React.useCallback(() => {
    const scroll = ref.current?.getBoundingClientRect();

    if (scroll) {
      // Check if the right end of the image element has reached the right end of the browser window
      const isRightEndReached = scroll.right <= browserStatus.width;
      setIsRightEndRendered(isRightEndReached);
    }
  }, [browserStatus.width, ref]);

  React.useEffect(() => {
    scrollEvent();
    window.addEventListener("scroll", scrollEvent);
    return () => {
      window.removeEventListener("scroll", scrollEvent);
    };
  }, [scrollEvent]);

  return isRightEndRendered;
};

export default useRightEndRendered;
